<template>
    <div class="form-general">
        <v-form @submit.prevent="addEvaluation()">
            <v-row>
               <v-col cols="12" md="4">
                   <h4 class="title">
                       Experiencia en docente universitario
                   </h4>
                   <v-text-field
                        color="secondary"
                        v-model.number="evaluation.items.numAnios"
                        label="Escriba sus años"
