                    ></v-text-field>
               </v-col>

               <v-col cols="12">
                   <h4 class="title">
                       Disponibilidad de horario, incluyendo fines de semana
                   </h4>
                   <v-radio-group v-model="evaluation.items.disponibilidad">
                    <v-radio
                        label="SI"
                        value="5"
                        color="secondary"
                    ></v-radio>
                    <v-radio
                        label="NO"
                        value="0"
                        color="secondary"
                    ></v-radio>
                    </v-radio-group>
               </v-col>

               <v-col cols="12">
                   <h4 class="title">
                       Disposición en entrevista
                   </h4>
                   <v-radio-group v-model="evaluation.items.disposicion">
                    <v-radio
                        label="SI"
                        value="5"
                        color="secondary"
                    ></v-radio>
                    <v-radio
                        label="NO"
