<script>
import router from '../router/index';
export default {
    name: 'Form-evaluation',
    data() {
        return {
            optConocimientos:[3, 6, 9, 12, 15],
            optPowerpoint: [1, 2, 3, 4, 5],
            opteduDistancia: [4,8,12,16,20],
            optLMS: [3,6,9,12,15],
            optResponsabilidad: [1,2,3,4,5],
            optComunicacion: [1,2,3,4,5],
            optInvestigacion: [2,4,6,8,10],
            optInteractuar: [1,2,3,4,5],

            evaluation:{
                items:{
                    numAnios: null,
                    anios: null,
                    disponibilidad: null,
                    disposicion: null,
                    conocimiento: null,
                    powerpoint: null,
                    eduDistancia: null,
                    LMS: null,
                    responsabilidad: null,
                    comunicacion: null,
                    investigacion: null,
                    interactuar: null
                },
                total_score: null,
                IdGeneral: ''
            }
        }
    },
    methods: {
        addEvaluation(){
            if(this.evaluation.items.numAnios >= 5){
                this.evaluation.items.anios = 20
            }
            this.evaluation.total_score = this.evaluation.items.anios + this.evaluation.items.conocimiento + this.evaluation.items.powerpoint + this.evaluation.items.eduDistancia + this.evaluation.items.LMS + this.evaluation.items.responsabilidad + this.evaluation.items.comunicacion + this.evaluation.items.investigacion + this.evaluation.items.interactuar
            this.evaluation.IdGeneral = this.$route.params.id
            
            this.axios.post('evaluations/new-evaluation', this.evaluation)
                .then((res) => {
                   router.push('/finish')
                }).catch((err) => {
                    console.log(err)
                });
        }
    }
}
</script>